import axios from "axios"
const User = {
    namespaced : true,
    actions:{
        //
        queryUsername(context){
            axios({
                method:"get",
                url:"http://index.nat100.top/user/indexshow"
            }).then(resp=>{
                context.commit('QUERYUSERNAME',resp.data)
            })
        },
        //
        querUserInformation(context){
            axios({
                method:"get",
                url:"http://index.nat100.top/userInformation/selectUserInformationAll?account="+localStorage.getItem('username')
            }).then(resp=>{
                context.commit('QUERYUSERINFORMATION',resp.data)
            })
        },
        //
        queryPassword(context){
            axios({
                method:"get",
                url:"http://index.nat100.top/user/selectPassword?username="+localStorage.getItem('username')
            }).then(resp=>{
                context.commit('QUERYPASSWORD',resp.data)
            })
        }
    },
    mutations:{
        QUERYUSERNAME(state,username){
            state.username = username
        },
        QUERYUSERINFORMATION(state,userInformation){
            state.avatarPath = userInformation.avatarPath === null ? require('../assets/images/logo-mi2.png') : userInformation.avatarPath
            state.nickname = userInformation.nickname
            state.sexvalue = userInformation.sex
            if(userInformation.sex === '1'){
                state.sex = ''
            }
            else{
                state.sex = ''
            }
            state.city = userInformation.city
        },
        QUERYPASSWORD(state,password){
            state.password = password
        }
    },
    state:{
        username : '',//
        avatarPath:'',//
        nickname : '',//
        sex : '',//
        city : '',//,
        password:'',//
        sexvalue :''//
    }
}

export default User