<!--  -->
<template>
    <!-- total: -->
    <!-- current-page : -->
    <!-- page-size  -->
    <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="$store.state.BrandAbout.currentPage"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="$store.state.BrandAbout.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="$store.state.BrandAbout.totalCount">
    </el-pagination>
</template>

<script>
    const EngineerPagination = {
        name : 'EngineerPagination',
        data() {
            return {
                // 
                pageSize:5,
                // 
                totalCount:100,
                // 
                currentPage: 1,
            }
        },
        methods: {
            //
            handleSizeChange(val) {
                //console.log(` ${val} `);
                // 
                this.$store.dispatch('BrandAbout/newPageSize',val)
                this.$store.dispatch('BrandAbout/queryBrands')
                // this.selectAll();
            },
            handleCurrentChange(val) {
                //console.log(`: ${val}`);
                // 
                this.$store.dispatch('BrandAbout/newCurrentPage',val)
                this.$store.dispatch('BrandAbout/queryBrands')
            }
        }
    }
    export default EngineerPagination
</script>

<style scoped>
    .el-pagination {
        position: fixed;
        bottom: 0;
    }
</style>