<!--  -->
<template>
    <el-table
            :data="$store.state.BrandAbout.tableData"
            style="width: 100%"
            :row-class-name="tableRowClassName"
            @selection-change="handleSelectionChange">
        <el-table-column
                type="selection"
                width="55">
        </el-table-column>
        <el-table-column
                type="index"
                width="50">
        </el-table-column>
        <el-table-column
                prop="brandName"
                label=""
                align="center"
        >
        </el-table-column>
        <el-table-column
                prop="companyName"
                label=""
                align="center"
        >
        </el-table-column>
        <el-table-column
                prop="ordered"
                align="center"
                label="">
        </el-table-column>
        <el-table-column
                prop="statusStr"
                align="center"
                label="">
        </el-table-column>
        <el-table-column
                align="center"
                label="">
                <!-- slot-scope -->
                <template slot-scope="scope">
                    <el-button type="primary" v-on:click="changeShow(scope.row)"></el-button>
                    <el-button type="danger" v-on:click="deleteById(scope.row)"></el-button>
                </template>
        </el-table-column>
    </el-table>
</template>

<script>
    import axios from 'axios';
    const EngineerBrandTable = {
        name : 'EngineerBrandTable',
        data(){
            return {
                multipleSelection:[],//
            }
        },
        methods: {
            tableRowClassName({row, rowIndex}) {
                if (rowIndex === 1) {
                    return 'warning-row';
                } else if (rowIndex === 3) {
                    return 'success-row';
                }
                return '';
            },
            // 
            handleSelectionChange(val) {
                this.multipleSelection = val;
                // console.log(this.multipleSelection)
                //id
                this.$bus.$emit('selectBrands',this.multipleSelection)
            },
            // 
            changeShow(brand){
                this.$bus.$emit('isDialogVisible',true)
                //
                let showData = {
                    status: '',
                    brandName: '',
                    companyName: '',
                    id: "",
                    ordered: "",
                    imageUrl:"",
                    flag: "1"
                }
                //id
                axios({
                    method:"get",
                    url:"http://index.nat100.top/brand/selectImgUrlByIdServlet?id="+brand.id
                }).then((resp) =>{
                    showData.imageUrl = resp.data;
                    //id
                    axios({
                        method:"get",
                        url:"http://index.nat100.top/brand/ChangeById?id="+brand.id
                    }).then((resp) => {
                        showData.status = resp.data.status;
                        showData.brandName = resp.data.brandName;
                        showData.companyName = resp.data.companyName;
                        showData.id = resp.data.id;
                        showData.flag = resp.data.flag;
                        showData.ordered = resp.data.ordered;
                        //brand,
                        this.$store.dispatch('BrandAbout/brandInformation',showData)
                    })
                })
            },
            //id
            deleteById(brand){
                let id = brand.id;//id
                let brandName;
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    //   
                    //id
                    axios({
                        method:"get",
                        url:"http://index.nat100.top/brand/selectBrandNameById?id="+id
                    }).then((resp) =>{
                        brandName = resp.data;
                        // 
                        axios({
                            method:"get",
                            url:"http://index.nat100.top/brand/deleteImageUrl?brandName="+brandName
                        }).then((resp)=>{
                                axios({
                                    method:"get",
                                    url:"http://index.nat100.top/brand/deleteById?id="+id
                                }).then((resp)=>{
                                    if(resp.data === "success"){
                                        // 
                                        this.$store.dispatch('BrandAbout/queryBrands')
                                        // 
                                        this.$notify({
                                            title: '',
                                            message: '',
                                            type: 'success'
                                        })
                                    }
                                })

                        })
                    })
                }).catch(() => {
                    //
                    this.$message({
                        type: 'info',
                        message: ''
                    })
                })
            },
        }
    }
    export default EngineerBrandTable
</script>