//vue
import Vue from "vue";

//vuex 
import Vuex from 'vuex'

//vuex
import shoppignCar from "./shoppingCar";
import Brand from "./Brand";
import Engineer from "./Engineer";
import User from "./User";
//vuex
Vue.use(Vuex)

const store = new Vuex.Store({
    modules:{
        ShoppingCarAbout:shoppignCar,
        BrandAbout : Brand,
        EngineerAbout : Engineer,
        UserAbout : User
    }
})

export default store
