<template>
        <el-table
            :data="removeBrand"
            style="width: 100%"
            :row-class-name="tableRowClassName">
        <el-table-column
                type="index"
                width="50">
        </el-table-column>
        <el-table-column
                prop="brandName"
                label=""
                align="center"
        >
        </el-table-column>
        <el-table-column
                prop="companyName"
                label=""
                align="center"
        >
        </el-table-column>
        <el-table-column
                prop="ordered"
                align="center"
                label="">
        </el-table-column>
        <el-table-column
                prop="statusStr"
                align="center"
                label="">
        </el-table-column>
        <el-table-column
                align="center"
                label="">
                <!-- slot-scope -->
                <template slot-scope="scope">
                    <el-button type="primary" v-on:click="restoreBrand(scope.row)"></el-button>
                </template>
        </el-table-column>
    </el-table>
</template>

<script>
    import axios from 'axios';
    const EngineerLogTable = {
        name : 'EngineerLogTable',
        computed:{
            removeBrand:{
                get(){
                    return this.$store.state.BrandAbout.removeBrand
                }
            }
        },
        methods: {
            tableRowClassName({row, rowIndex}) {
                if (rowIndex === 1) {
                    return 'warning-row';
                } else if (rowIndex === 3) {
                    return 'success-row';
                }
                return '';
            },
            //,,
            restoreBrand(brand){
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    axios({
                        method:"get",
                        url:"http://index.nat100.top/brand/updateRemoveBrand?id="+brand.id
                    }).then(()=>{
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                        //
                        this.$store.dispatch('BrandAbout/removeBrand')
                })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });          
                });                
            }
        }
    }
    export default EngineerLogTable
</script>