<template>
    <!---->
    <el-form :inline="true" :model="$store.state.BrandAbout.brand" class="demo-form-inline">
            <el-form-item label="" v-show="title===''">
                <el-select v-model="$store.state.BrandAbout.brand.status" placeholder="">
                    <el-option label="" value="1"></el-option>
                    <el-option label="" value="0"></el-option>
                </el-select>
            </el-form-item>

            <el-form-item label="">
                <el-input v-model="$store.state.BrandAbout.brand.companyName" placeholder=""></el-input>
            </el-form-item>

            <el-form-item label="">
                <el-input v-model="$store.state.BrandAbout.brand.brandName" placeholder=""></el-input>
            </el-form-item>

            <el-form-item>
                <el-button type="primary" id="submit_btn" v-on:click="onSubmit"></el-button>
            </el-form-item>
    </el-form>
</template>

<script>
import axios from 'axios';

    const EngineerSearch = {
        name : 'EngineerSearch',
        props:['title'],
        methods: {
            onSubmit(){
                //title, , ,
                if(this.title === ''){
                    this.$store.dispatch('BrandAbout/queryBrands')
                } 
                else if(this.title === ''){
                    let brand = {
                        brandName:this.$store.state.BrandAbout.brand.brandName,
                        companyName:this.$store.state.BrandAbout.brand.companyName
                    }
                    this.$store.dispatch('BrandAbout/searchRemoveBrand',brand)
                }
                else{
                    if(this.$store.state.BrandAbout.brand.brandName === '' || this.$store.state.BrandAbout.brand.companyName === ''){
                        this.$message({
                            message: '',
                            type: 'error'
                        })
                    }
                    else{
                        axios({
                            method : 'get',
                            url : 'http://index.nat100.top/brand/selectConfirmBrandServlet?brandName='+this.$store.state.BrandAbout.brand.brandName+'&companyName='+this.$store.state.BrandAbout.brand.companyName
                        }).then(resp=>{
                            if(resp.data !== null){
                                this.$store.state.BrandAbout.brand.brandName = resp.data.brandName
                                this.$store.state.BrandAbout.brand.imageUrl = resp.data.imageUrl
                            }
                            else{
                                this.$message({
                                    message: '',
                                    type: 'error'
                                });
                            }
                        })
                    }
                }
            }
        },
    }
    export default EngineerSearch
</script>