<template>
    <!--            -->
    <div class="change_telephone" v-show="telIsShow">
        <ul class="change_telephone_flex">
            <input type="number" placeholder="" class="in_telephone_one" v-model="telephone">
            <input type="number" placeholder="" class="in_telephone_two" v-model="confirmTelephone">
        </ul>
        <button class="telephone_submit" @click="changeTel"></button>
        <button class="telephone_close" v-on:click="closeTel"></button>
    </div>
</template>

<script>
    import axios from 'axios'
    const EngineerChangeTelephone = {
        name : 'EngineerChangeTelephone',
        data() {
            return {
                telIsShow:false,
                telephone:'',
                confirmTelephone:''
            }
        },
        methods: {
            //
            closeTel(){
                this.telIsShow = false
            },
            //
            changeTel(){
                //
                if(this.telephone === '' || this.confirmTelephone === ' '){
                    this.$message({
                        type: 'error',
                        message: ''
                    })
                }
                //,
                else if(this.telephone === this.confirmTelephone){
                    //,,
                    axios({
                        method:'get',
                        url : 'http://index.nat100.top/engineer/updateTelephone?telephone='+this.telephone
                    }).then(resp=>{
                        if(resp.data === 'success'){
                            this.$message({
                                type: 'success',
                                message: ''
                            })
                            this.telIsShow = false//
                            //
                            this.$store.dispatch('EngineerAbout/queryEngineerInformation')
                        }
                        else{
                            this.$message({
                                type: 'success',
                                message: ''
                            })
                        }
                    })
                }
                else{
                    this.$message({
                        type: 'error',
                        message: ''
                    })
                }
            }
        },
        mounted() {
            this.$bus.$on('isCloseTel',(show)=>{
                this.telIsShow = show
            })
        }
    }
    export default EngineerChangeTelephone
</script>

<style scoped>
    .change_telephone {
        position: absolute;
        top: 55px;
        left: -45px;
        z-index: 2;
        width: 370px;
        height: 270px;
        background-color: #eef7f2;
    }
    .change_telephone .change_telephone_flex {
        display: flex;
        flex-direction: column;
        margin-left: 37px;
        margin-top: 45px;
    }
    .change_telephone .change_telephone_flex input:nth-child(-n+1) {
        margin-bottom: 25px;
    }
    .change_telephone .change_telephone_flex .in_telephone_two {
        margin-top: 20px;
    }
    .change_telephone input {
        outline-style: none;
        border: 1px solid #ccc;
        border-radius: 3px;
        padding: 13px 14px;
        width: 280px;
        font-size: 14px;
        font-weight: 700;
        font-family: "Microsoft soft";
        margin: 2px;
    }
    .change_telephone button {
        background-color: #1795bb;
        border-radius: 12px;
        border: none;
        color: white;
        padding: 16px 32px;
        text-align: center;
        text-decoration: none;
        display: inline-block;
        font-size: 16px;
        margin: 4px 43px;
        -webkit-transition-duration: 0.4s;
        transition-duration: 0.4s;
        cursor: pointer;
    }
</style>