<!--  -->
<template>
    <!---->
    <el-dialog
               title=""
               :visible.sync="getmessage"
               width="30%"
       >
           <el-form ref="form" v-bind:model="brand" label-width="80px">
               <el-form-item label="">
                   <el-input v-model="brand.brandName"></el-input>
               </el-form-item>

               <el-form-item label="">
                   <el-input v-model="brand.companyName"></el-input>
               </el-form-item>

               <el-form-item label="">
                   <el-input v-model="brand.ordered"></el-input>
               </el-form-item>

               <el-form-item label="">
                   <el-input type="textarea" v-model="brand.imageUrl"></el-input>
               </el-form-item>

               <el-form-item label="">
                   <el-switch v-model="brand.status"
                              active-value="1"
                              inactive-value="0"
                   ></el-switch>
               </el-form-item>


               <el-form-item>
                   <el-button type="primary" v-on:click="addBrand"></el-button>
                   <el-button @click="getmessage = false"></el-button>
               </el-form-item>
           </el-form>

    </el-dialog>
</template>

<script>
    import axios from 'axios';
    const EngineerAddBrand = {
        name : 'EngineerAddBrand',
        data() {
            return {
                 //
                 getmessage:false,
                 //
                 brand: {
                    status: '',
                    brandName: '',
                    companyName: '',
                    id: "",
                    ordered: "",
                    imageUrl:"",
                    flag: "1"
                }
            }
        },
        methods: {
            isGetmessage(value){
                this.getmessage = value
            },
            //
            addBrand() {
                if(this.brand.status === ""){
                    this.brand.status = "0"
                }
                if(this.brand.brandName === "" || this.brand.companyName === "" || this.brand.imageUrl === "" || this.brand.ordered === "" || this.brand.imageUrl === ""){
                    this.$message({
                        showClose: true,
                        message: '',
                        type: 'error'
                    })
                }
                else{
                    // ajax
                    //
                    axios({
                        method:"get",
                        url:"http://index.nat100.top/brand/selectImageUrl?imageUrl="+this.brand.imageUrl+"&id="+0
                    }).then((resp) =>{
                        if(resp.data === "exist"){
                            this.$message({
                                showClose: true,
                                message: '',
                                type: 'error'
                            });
                        }
                        else{
                            //
                            axios({
                                method:"get",
                                url:"http://index.nat100.top/brand/selectRepeat?brandName="+this.brand.brandName+"&companyName="+this.brand.companyName+"&id="+0
                            }).then((resp) => {
                                if (resp.data === 0) {
                                    this.$message({
                                        showClose: true,
                                        message: ',',
                                        type: 'error'
                                    });
                                    //
                                    this.getmessage = false;
                                }
                                else if (resp.data === 1) {
                                    this.$message({
                                        showClose: true,
                                        message: '',
                                        type: 'error'
                                    });
                                    //
                                    this.getmessage = false;
                                }
                                else if(resp.data === "no_repeat"){
                                    //
                                    if(this.brand.status === ""){
                                        this.brand.status = ""
                                    }
                                    // ajax
                                    // console.log('json_brand',JSON.stringify(brand));
                                    axios({
                                        method:"post",
                                        url:"http://index.nat100.top/brand/addBrandServlet",
                                        data:JSON.stringify(this.brand)
                                    }).then((resp) => {
                                        if(resp.data === "success"){
                                            this.$message({
                                                showClose: true,
                                                message: '',
                                                type: 'success'
                                            })
                                            this.$store.dispatch('BrandAbout/queryBrands')
                                            this.getmessage = false
                                            //
                                            this.brand = {
                                                status: '',
                                                brandName: '',
                                                companyName: '',
                                                id: "",
                                                ordered: "",
                                                imageUrl:"",
                                                flag: "1"
                                            }
                                        }
                                    }) 
                                }
                            })

                        }
                    })
                }
            }
        },
        mounted() {
            //,getmessage
            this.$bus.$on('isGetmessage',this.isGetmessage)
        },
        beforeDestroy() {
            //
            this.$bus.$off(['isGetmessage'])
        }
    }
    export default EngineerAddBrand
</script>