<template>
  <div class="shoppingBrand_root">
    <!--  -->
    <BuyBrandHeader titleNameLogo="" titleNameHeader=""></BuyBrandHeader>
    <!--  -->
    <ShoppingCarAside></ShoppingCarAside>
    <!--  -->
    <router-view></router-view>
    <!---->
    <div id="hidebg" v-show="isHidebg"></div>
  </div>
</template>

<script>
    import BuyBrandHeader from '@/components/BuyBrandHeader.vue';
    import ShoppingCarAside from '@/components/ShoppingCarAside.vue';
    const ShoppingCar = {
        name : 'ShoppingCar',
        components:{
            BuyBrandHeader : BuyBrandHeader,
            ShoppingCarAside : ShoppingCarAside
        },
        data(){
          return {
            //
            isHidebg:false
          }
        },
        methods: {
          isShowHidebg(data){
            this.isHidebg = data
          }
        },
        mounted() {
          //,isChange,
          this.$bus.$on('isChangeHidebg',this.isShowHidebg)
        },
        beforeDestroy() {
          //
          this.$bus.$off(['isChangeHidebg'])
        }
    }
    export default ShoppingCar
</script>

<style scoped>
  #hidebg {
      position:absolute;
      left:0px;
      top:0px;
      background-color:#000;
      width:100%;  /*100%*/
      height: 100%;
      filter:alpha(opacity=60);  /*60%*/
      opacity:0.6;  /*IE60%*/
      z-Index:8;
  }
  .shoppingBrand_root{
    height: 100vh;
    overflow: hidden;
    background-color: #fffdfd;
  }
</style>