import axios from "axios"

const Brand = {
    namespaced:true,
    actions:{
        //, ()
        queryBrands(context){
            axios({
                method:"post",
                url:"http://index.nat100.top/brand/selectByPageAndCondition?currentPage="+context.state.currentPage+"&pageSize="+context.state.pageSize,
                data:JSON.stringify(context.state.brand)
            }).then(resp =>{
                // resp.data.rows {rows:[],totalCount:100}
                // resp.data.totalCount
                let respArr = [resp.data.rows,resp.data.totalCount]
                context.commit('QUERYBRANDS',respArr)
            })
        },
        //
        newPageSize(context,value){
            context.commit('NEWPAGESIZE',value)
        },
        //
        newCurrentPage(context,value){
            context.commit('NEWCURRENTPAGE',value)
        },
        //brand
        brandInformation(context,brand){
            context.commit('BRANDINFORMATION',brand)
        },
        //
        removeBrand(context){
            axios({
                method:"get",
                url:"http://index.nat100.top/brand/selectRemoveBrand"
            }).then(resp=>{
                //resp.data
                context.commit('REMOVEBRAND',resp.data)
            })
        },
        //
        searchRemoveBrand(context,brand){
            axios({
                method:"get",
                url:"http://index.nat100.top/brand/selectLikeRemoveBrandServlet?brandName="+brand.brandName+"&companyName="+brand.companyName
            }).then(resp=>{
                context.commit('REMOVEBRAND',resp.data)
            })
        },
        //
        searchUserBuyBrand(context){
            axios({
                method: "get",
                url: "http://index.nat100.top/brand/viewBrandBuy?id=" + sessionStorage.getItem('BrandId')
            }).then(resp=>{
                context.commit('SEARCHUSERBUYBRAND',resp.data)
            })
        }
    },
    mutations:{
        QUERYBRANDS(state,respArr){
            state.tableData = respArr[0],
            state.totalCount = respArr[1]
        },
        //
        NEWPAGESIZE(state,value){
            state.pageSize = value
        },
        //
        NEWCURRENTPAGE(state,value){
            state.currentPage = value
        },
        //brand
        BRANDINFORMATION(state,brand){
            state.brand = brand
        },
        //
        REMOVEBRAND(state,value){
            state.removeBrand = value
        },
        //
        SEARCHUSERBUYBRAND(state,value){
            state.queryBrand=value
        }
    },
    state:{
        // 
        tableData: [],
        //
        removeBrand: [],
        //
        brand: {
            status: '',
            brandName: '',
            companyName: '',
            id: "",
            ordered: "",
            imageUrl:"",
            flag: "1"
        },
        // 
        pageSize:5,
        // 
        totalCount:100,
        // 
        currentPage: 1,
        //
        queryBrand:{}
    }
}

export default Brand