<template>
    <!--  -->
    <el-row>
        <el-button type="danger" plain v-on:click="deleteByIds"></el-button>
        <el-button type="primary" plain v-on:click="isGetmessage"></el-button>
    <!--     ,-->
        <el-button type="info" plain v-on:click="backBrand"></el-button>
    </el-row>
</template>

<script>
    import axios from 'axios'
    const EngineerFunction = {
        name : 'EngineerFunction',
        data() {
            return {
                selectIds:[]
            }
        },
        methods: {
            isGetmessage(){
                this.$bus.$emit('isGetmessage',true)
            },
            //
            deleteByIds(){
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    //
                    //AJAX
                    //
                    axios({
                        method:"post",
                        url:"http://index.nat100.top/brand/deleteByIds",
                        data:JSON.stringify(this.selectIds)
                    }).then((resp) => {
                        if(resp.data === "success"){
                            //
                            // 
                            this.$store.dispatch('BrandAbout/queryBrands')
                            // 
                            this.$message({
                                message: '',
                                type: 'success'
                            })
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    })         
                })
            },
            // 
            backBrand(){
                this.$store.state.BrandAbout.brand={
                    status: '',
                    brandName: '',
                    companyName: '',
                    id: "",
                    ordered: "",
                    imageUrl:"",
                    flag: "1"
                }
                this.$store.state.BrandAbout.pageSize = 5
                this.$store.state.BrandAbout.currentPage = 1
                this.$store.dispatch('BrandAbout/queryBrands')
            }
        },
        mounted() {
            //id
            this.$bus.$on('selectBrands',(multipleSelection)=>{
                for(let i = 0 ; i < multipleSelection.length ; i++){
                    this.selectIds[i] = multipleSelection[i].id
                }
            })
        },
        beforeDestroy() {
            this.$bus.$off(['selectBrands'])
        },
    }
    export default EngineerFunction
</script>